/* Ïò§Îäò ÎÇ†Ïßú Ï∂îÏ∂ú */
function getToday(){
    const dt = new Date()
    let year = dt.getFullYear()
    let month = (dt.getMonth()+1).toString()
    month = month.length == 1 ? '0' + month : month
    let day = dt.getDate().toString()
    day = day.length == 1 ? '0' + day : day
    return `${year}-${month}-${day}`
}

